<template>
  <div>

    <div v-if="homedata&&homedata.length>0" class="container-mini">
      <div class="content">
        <van-list
          ref="dida_list"
          class="common-list"
        >
          <div
            v-for="(item, index) in homedata"
            :key="index"
          >
            <!-- <mouldsList class="content" style="margin-top:10px;" :data-item="item" :wybm-list-item="wybmListItem" :fbxx="fbxx" show-more="false" /> -->
            <div v-if="item.ggnrStr || item.ggnrStr==='' " class="common-main box">
              <div class="title title-page">
                <div class="title-left">
                  <h2>邀请函内容</h2>
                </div>
              </div>
              <div class="notice-content" v-html="item.ggnrStr" />
              <div v-if="item.sftyyq=== 0 || item.sftyyq=== null" class="foot-btn noshadow">
                <ul>
                  <li><van-button type="info" size="large" round @click="yqhqrAction(0)">拒绝邀请</van-button></li>
                  <li><van-button type="info" size="large" round plain @click="yqhqrAction(1)">接受邀请</van-button></li>
                </ul>
              </div>
              <div v-else-if="item.sftyyq=== 1" class="foot-btn noshadow">
                <ul>
                  <li><van-button type="info" size="large" round disabled>已接受邀请</van-button></li>
                </ul>
              </div>
              <div v-else />
            </div>
            <div v-else>
              <yqhMouldsList v-if="sfdlbb ==1" :data-item="item" :show-more="showMore" @goMoreAction="MoreAction($event)" />
            </div>
          </div>
        </van-list>
      </div>
    </div>
    <div v-else>
      <error-page
        :page-style="pageStyles"
        @errorButton="parentReceive"
      />
    </div>
  </div>
</template>

<script land="script" scoped>
import { mapState } from 'vuex'
import yqhMouldsList from '@/components/list/yqhMouldsList.vue'
import errorPage from '@/components/result-page/index'

export default {
  name: 'Yqhqr',
  components: {
    yqhMouldsList,
    errorPage
  },
  props: {
    appsrvurl: {
      type: null,
      default: ''
    },
    xmid: {
      type: null,
      default: ''
    },
    sfdlbb: {
      type: null,
      default: ''
    }
  },
  data() {
    return {
      showMore: false,
      fbxx: '',
      pageStyles: 0,
      ind: '', // 点击事件
      pageUrl: '',
      homedata: []

    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.mh.mhUserInfo
    })
  },
  created() {
    const _this = this
    this.$bus.$on('yqhChange', function() {
      _this.getggData()
    })
    if (this.$store.state.gyswdxm.yqhqr) {
      this.homedata = this.$store.state.gyswdxm.yqhqr
    } else {
      this.getggData()
    }
  },
  methods: {
    parentReceive() {
      this.getggData()
    },
    goBm() {
      this.$router.push({
        name: 'Xmfb',
        params: { ids: this.checkIds
        }})
    },
    yqhqrAction(type) {
      this.$store.dispatch('gyswybm/getGysyqhqrAction', {
        zzid: this.userInfo.zzid,
        xmid: this.xmid,
        sftyyq: type
      }).then((res) => {
        this.res = res
        this.$store.commit('gyswybm/SET_YQHZT', type)
        if (type === 1) {
          this.$router.push({
            name: 'Xmfb',
            params: { ids: this.checkIds
            }})
        } else {
          this.$router.push({
            name: 'Yqhlist' })
        }
      }).catch(error => {
        this.$toast.fail(error)
      })
    },
    getggData() {
      this.$store.dispatch('gyswdxm/getGysJdxqAction', { xmid: this.xmid, zzid: this.userInfo.zzid, appsrvurl: this.appsrvurl }).then(res => {
        if (res) {
          this.homedata = res
          this.pageStyles = 0
        } else {
          this.pageStyles = 1
        }
        this.$store.commit('gyswdxm/SET_YQHQR', res)
      }).catch((error) => {
        this.pageStyles = 3
        this.$toast(error)
      })
    },
    MoreAction(item) {
      console.log('MoreAction======', item)
      const more = []
      more.push(item.showmore)
      more.push(item.title)
      this.$router.push({
        name: 'DbsxSjbqMore',
        params: { morexx: more }
      })
    }

  }

}
</script>
<style lang="css" scoped>
  .nav-bar{box-shadow: none;}
</style>

